.landing {
	.section {
		position: relative;
		line-height: 0px;
		display: flex;
		justify-content: center;

		.logo {
			display: flex;
			align-items: center;
			position: absolute;
			left: 0px;
			top: -10px;

			img {
				width: 177px;
				height: 177px;
			}

			.logotitle {
				font-family: 'Edo', sans-serif;
				font-size: 30px;
				line-height: 40px;
			}
		}

		.banner {
			background-image: url(https://i.ibb.co/fGRKfjn/banner.png);
			background-position-y: center;
			background-repeat: no-repeat;
			background-size: cover;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.mask1 {
			-webkit-mask-image: url(https://i.ibb.co/vwWnFsq/mask1.png);
			mask-image: url(https://i.ibb.co/vwWnFsq/mask1.png);
			-webkit-mask-size: 100%;
			mask-size: 100%;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			filter: drop-shadow(15px 15px 0 #a53e6f) drop-shadow(-15px -15px 0 #a53e6f);
			width: 92%;
		}

		.backgroundtext {
			position: absolute;
			font-size: 139px;
			font-family: 'Edo', sans-serif;
			width: 995px;
			height: 185px;
			line-height: 185px;
			text-align: center;
		}

		.parallax {
			/* The image used */
			background-image: url(https://i.postimg.cc/1P4yrCNj/background.png);

			/* Set a specific height */
			min-height: 90%;

			/* Create the parallax scrolling effect */
			background-attachment: fixed;
			background-position-y: 22%;
			background-repeat: no-repeat;
			background-size: cover;
		}
	}

	.section:not(:nth-child(2)) {
		margin-left: 60px;
		margin-right: 60px;
	}

	.journey {
		position: absolute;
		justify-content: center;
		display: flex;
		// margin-left: calc(100% - 8px);
		bottom: 0px;
		margin-left: 7px;
	}

	.journey div {
		cursor: pointer;
		position: relative;

	}

	.journey div img {
		position: absolute;
		top: -193px;
		width: 386px;
		left: -5px;
		z-index: 1;
	}

	.journeybtn {
		background-color: #A53E6F;
		border-radius: 50px;
		font-size: 34px;
		line-height: 25px;
		color: white;
		/* width: auto; */
		font-family: "Monster AG", sans-serif;
		width: 380px;
		display: flex;
		height: 103px;
		text-align: center;
		align-items: center;
		justify-content: center;
		position: relative;
	}

	.journeybtn::before, .journeybtn::after {
		content: '';
		position: absolute;
		left: -2px;
		top: -2px;
		background: linear-gradient(45deg, #e6fb04, #ff6600, #00ff66, #00ffff, #ff00ff, #ff0099, #6e0dd0, #ff3300, #099fff);
		background-size: 400%;
		width: calc(100% + 5px);
		height: calc(100% + 5px);
		z-index: -1;
		animation: animate 20s linear infinite;
		border-radius: 52px;
	}

	@keyframes animate {
		0% {
			background-position: 0 0;
		}
		50% {
			background-position: 400% 0;
		}
		100% {
			background-position: 0 0;
		}
	}

	.journeybtn::after {
		filter: blur(20px);
		opacity: 0.5;
	}

	.description {
		margin-top: 76px;
		display: flex;
		align-items: center;
		justify-content: center;

		img {
			width: 75%;
		}
	}

	.section-1 {
		margin-top: 64px;
		align-items: center;

		.journeybtn1 {
			display: none;
		}
	}

	.down img {
		width: 41px;
		height: 24px;
	}

	.section-1 .contact-group {
		position: absolute;
		display: flex;
		align-items: center;
		right: 0px;
	}

	.section-2 {
		display: flex;
		align-items: center;
		justify-content: flex-start;

		>img {
			width: 300px;
		}

		.mineral-help {
			margin-left: 100px;

			img {
				display: none;
			}
		}
	}

	.section-3 {
		border: 15px solid #A53E6F;
		border-radius: 25px;
		margin-top: 117px;
		background-image: url("https://i.postimg.cc/1P4yrCNj/background.png");
		background-attachment: fixed;
		background-position-y: 60%;
		background-repeat: no-repeat;
		background-size: cover;
		min-height: 400px;
	}

	.section-4 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 550px;
		}

		.mandrills-help img {
			display: none;
		}
	}


	.section-5 {
		align-items: center;
		margin-top: 100px;

		>img {
			width: 420px;
		}

		.participate-help {
			margin-left: 60px;

			img {
				display: none;
			}
		}
	}



