.Loading {
    background-image: url(../../img/loadingback.jpg);
    height: 100vh;
    background-size: cover;
    position: absolute;
    top: 0px;
    z-index: 100;
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    font-family: "Edo", sans-serif;

    .title {
        margin-top: 125px;
        font-size: 139px;
    }

    .loadingImg {
        margin-top: 119px;

        .loadingText {
            font-size: 32px;
            margin-top: 29px;
            text-align: center;
        }
    }

    @media only screen and (max-width: 1536px) {
        .title {
            font-size: 98px;
            margin-top: 130px;
        }

        .loadingImg {
            margin-top: 85px;
            width: 257px;

            .loadingText {
                font-size: 22px;
            }
        }
    }

    @media only screen and (max-width: 1366px) {
        .title {
            font-size: 98px;
            margin-top: 130px;
        }

        .loadingImg {
            margin-top: 85px;
            width: 257px;

            .loadingText {
                font-size: 22px;
            }
        }
    }

    @media only screen and (max-width: 1280px) {
        .title {
            font-size: 91px;
            margin-top: 94px;
        }

        .loadingImg {
            margin-top: 81px;
            width: 241px;

            .loadingText {
                font-size: 20px;
            }
        }
    }

    @media only screen and (max-width: 768px) {
        justify-content: center;
        .title {
            font-size: 54px;
            margin-top: 0px;
        }

        .loadingImg {
            margin-top: 48px;
            width: 145px;

            .loadingText {
                font-size: 11px;
            }
        }
    }

    @media only screen and (max-width: 650px) {
        justify-content: center;
        background-image: url(../../img/mobileLoadingback.png);
        background-size: cover;
        .title {
            font-size: 51px;
            margin-top: 0px;
        }

        .loadingImg {
            margin-top: 63px;
            width: 205px;

            .loadingText {
                font-size: 18px;
                margin-top: 22px;
            }
        }
    }

    @media only screen and (max-height: 450px) {
        justify-content: center;
        .title {
            font-size: 41px;
            margin-top: 0px;
        }

        .loadingImg {
            margin-top: 17px;
            width: 167px;

            .loadingText {
                font-size: 14px;
                margin-top: 13px;
            }
        }
    }

    @media only screen and (max-width: 360px) {
        justify-content: center;
        .title {
            font-size: 34px;
            margin-top: 0px;
        }

        .loadingImg {
            margin-top: 34px;
            width: 94px;

            .loadingText {
                font-size: 6px;
                margin-top: 6px;
            }
        }
    }
}